.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0006;
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s ease;
}

.popup__open,
.popup-payment__open {
  opacity: 1;
  visibility: visible;
}

.popup__content {
  width: 676px;
  padding: 32px;
  border-radius: 15px;
  background: rgb(255, 255, 255);
}

.popup__laible {
  margin-bottom: 8px;
  font-size: 16px;
  line-height: 22px;
  color: rgba(26, 31, 22, 0.5);
}

.input-form {
  border-radius: 13px;
  padding: 8px 16px;
  width: 596px;
  height: 59px;
  box-shadow: 0 4px 16px #1a1f1626;
  border: none;
  background: #fff;
  font-size: 20px;
  color: #1a1f1680;
}

.error-message {
  margin-bottom: 24px;
  font-size: 20px;
  color: red;
}

.save-btn {
  width: 100%;
  padding: 8px 24px;
  font-size: 20px;
  line-height: 24px;
  border-radius: 10px;
  color: #fff;
  background: #1a1f16;
}

.payment__info-form {
  display: flex;
  flex-direction: column;
}

.payment-label {
  margin-bottom: 16px;
  font-family: Cabin;
  font-size: 24px;
  line-height: 24px;
  cursor: pointer;
  color: rgba(26, 31, 22, 0.5);
}
